@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Poppins', sans-serif;
}
.app{
  max-width: 100vw;
  height: auto;
  min-height: 100vh;
  background-image: linear-gradient(to right, #4464AD, #492178);
  display: flex;
  justify-content: center;
  padding: 50px;
}
.app h1{
  text-transform: uppercase;
  color: #fff;
  font-weight: 700;
  font-size: 52px;
  letter-spacing: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.app .todo-list{
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: flex-start;
  align-items: center;
}
.app .todo-list form input{
  padding: 20px 10px;
  width: 500px;
  outline: none;
  background-color: #F4F4F4;
  font-size: 22px;
  color: #777;
  font-weight: bold;
  border: none;
}
.app .todo-list button{
  font-size: 22px;
  background-color: transparent;
  border: none;
}
.app .todo-list .todo{
  width: 500px;
  display: flex;
  justify-content: space-between;

}
.app .todo-list .todo p{
  display: flex;
  align-items: center;
  width: 350px;
  background-color: #f4f4f4;
  padding: 10px ;
  font-weight: 500;
  color: #333;
}
.app .todo-list .todo button{
  width: 50px;
  height: 100%;
  color: #fff;
  cursor: pointer;
}
.app .todo-list .todo button:first-child{
  background-color: #4464AD;
  color: #fff;
  height: 100%;
}
.app .todo-list .todo button:nth-child(2){
  background-color: #492178;
  height: 100%;
  color: #fff;
}
.app .todo-list .todo button:nth-child(3){
  background-color: rgb(255, 255, 255) ;
  color: rgb(218, 11, 11);
  height: 100%;
}
@media(max-width: 800px){
  .app h1{
  font-size: 28px;
  letter-spacing: 1px;
}
.app .todo-list form input{
  padding: 20px 10px;
  width: 350px;
}
.app .todo-list button{
  font-size: 22px;
  background-color: transparent;
  border: none;
}
.app .todo-list .todo p{
  display: flex;
  align-items: center;
  width: 200px;
}
.app .todo-list .todo{
  width: 350px;
}
.app .todo-list .todo button{
  width: 50px;
  height: 100%;
  color: #fff;
  cursor: pointer;
}
.app .todo-list .todo button:first-child{
  background-color: #4464AD;
  color: #fff;
  height: 100%;
}
.app .todo-list .todo button:nth-child(2){
  background-color: #492178;
  height: 100%;
  color: #fff;
}
.app .todo-list .todo button:nth-child(3){
  background-color: rgb(255, 255, 255) ;
  color: rgb(218, 11, 11);
  height: 100%;
}
}